<template>
    <b-row>
     <b-col sm="12">
      <div class="d-flex">
        <h1>Packges</h1>
        <b-button variant="success" size="sm" class="ml-auto mb-auto mt-auto" @click="$router.push({name:'Create Packge'})">Assign Package</b-button>
      </div>
      <!-- <div>
        <b-input-group class="mb-3" style="width:20em">
          <template #append>
            <b-input-group-btn variant="primary" >

              <b-button  variant="primary" @click="search()">
                 <CIcon name="cil-search" />
              </b-button>
            </b-input-group-btn>
          </template>
          <b-form-input placeholder="Search" v-model="keyword" v-on:keyup.enter="search()"></b-form-input>
        </b-input-group>
      </div> -->
      <b-card>
        <b-table  striped hover :items="items.data" :fields="fields" :busy="loading.main">
          <!--loading-->
          <template #table-busy>
            <div class="text-center text-danger ">
              <b-spinner class="align-middle" variant="primary" type="grow"></b-spinner>
            </div>
          </template>



          <template v-slot:cell(order) = row>
            <router-link v-if="row.item.order" :to="{name:'Edit Order', params:{id:row.item.order.id}}" style="color:#212529">{{row.item.order.title}}</router-link>

          </template>

          <template v-slot:cell(user) = row>
            <span v-if="row.item.user">{{row.item.user.first_name + ' ' + row.item.user.last_name}}</span>
          </template>


          <template v-slot:cell(status) = row>
              <b-form-select size="sm" class="w-auto" v-model="row.item.status" :options="statusOptions" @change="changeStatus(row.item)">
              </b-form-select>
          </template>


          <template v-slot:cell(created_at) = row >
            <span>{{ row.item.created_at | moment("from", "now") }}</span>
          </template>


          <!--actions-->
          <template v-slot:cell(actions) = row >

              <b-button
                variant="success"
                size="sm"
                class="ml-1 mr-1 small-btn"
                @click="$router.push({name:'Edit Packge', params:{id:row.item.id}})"
                >Edit</b-button>
              <b-button variant="danger" size="sm" class="ml-1 mr-1 small-btn"  @click="$bvModal.show(`${row.item.id}`)">delete</b-button>
              <b-modal :id="`${row.item.id}`" centered title="Delete" ok-variant="danger" ok-title="Yes" cancel-title="No" @ok="deleteItem(row.item.id)">
                <div>Are you sure that you want to delete this?</div>
              </b-modal>
          </template>

        </b-table>
        <div class="overflow-auto d-flex">
          <b-pagination-nav class="m-auto" @change="getData($route.query.page)" :link-gen="linkGen" :number-of-pages="items.last_page" use-router></b-pagination-nav>
        </div>
      </b-card>
    </b-col>

  </b-row>
</template>
<script>
import axios from 'axios'
import Vue from 'vue'
Vue.use(require('vue-moment'));
import {showToast } from '@/assets/js/index';
export default {
    name:"packges",
    data() {
        return {
            statusOptions:["enroute","delivered"],
            items:[],
            fields:[
                {key:"order", label:"Order Name"},
                {key:"user", label:"Driver"},
                {key:"id", label:"ID"},
                {key:"status", label:"Status"},
                {key: "created_at", label: "Created At"},
                {key: "actions", label: "Actions"}
            ],
            loading:{
                main:false
            }
        }
    },
    methods:{
        getItems() {
            this.loading.main = true
            axios.get(`/admin/v1/packages`)
            .then(res=>{
                this.items = res.data.data;
                this.loading.main = false
            })
            .catch(() => {
                this.loading.main = false
            })
        },
      linkGen(pageNum) {
        return pageNum === 1 ? '?' : `?page=${pageNum}`
      },
      getData(page) {
        this.loading.main = true
        axios.get(`admin/v1/packages?page=${page}`)
        .then((res) => {
          this.loading.main = false;
          console.log(res.data)
          this.items = res.data.data
        })
        .catch((error) => {
          this.loading.main = false;
          console.error(error)
        })
      },
        deleteItem(id){
            axios.delete(`/admin/v1/packages/${id}`)
            .then(()=>{
                showToast("success", "Success", "Item deleted successfully!", this)
                //remove deleted item
                this.items = this.items.filter(item=>{
                return item.id != id
                })
            })
            .catch(err=>{

                for(let error in err.response.data.errors) {
                    showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
                }
                console.log(err.response)
            })
        },

        changeStatus(item) {
            axios.put(`/admin/v1/packages/${item.id}`, {status:item.status})
            .then(()=>{
                showToast("success", "Success", `item status has been updated successfully to ${item.status}`, this)
            })
            .catch(err => {
                for(let error in err.response.data.errors) {
                    showToast('danger', `${error}`, `${err.response.data.errors[error][0]}`, this)
                }
            })
        },
    },

    mounted() {
        this.getItems();
    
    }
}
</script>